* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #000;
  height: 100%;
  background-color: #fafafa;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  font-size: 1em;
  line-height: 1.5;
}

.search-container {
  width: 100%;
  z-index: 10;
  background-color: #fafafa;
  padding-top: 10px;
  padding-bottom: 10px;
  position: fixed;
  box-shadow: 0 1px 2px 1px rgba(6, 6, 63, .3);
}

#search-form {
  font: inherit;
  width: 100%;
  background-color: #fafafa;
  flex-wrap: wrap;
  justify-content: space-around;
  margin-top: 20px;
  display: flex;
}

input {
  width: 50%;
  height: 50px;
  box-sizing: border-box;
  color: #fafafa;
  background: #2f303a;
  border: 1px solid rgba(255, 255, 255, .3);
  border-radius: 4px;
  outline: none;
  margin: 0 0 20px;
  padding: 1rem;
}

button {
  width: 20%;
  height: 50px;
  letter-spacing: .06em;
  color: #fff;
  cursor: pointer;
  background: #2196f3;
  border-radius: 4px;
  outline: none;
  font-size: 1rem;
  font-weight: 700;
  line-height: 1.9;
  transition-property: background;
  transition-duration: .25s;
  box-shadow: 0 4px 4px rgba(0, 0, 0, .15);
}

button:focus, button:hover {
  background: #188ce8;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
}

:root {
  --timing-function: cubic-bezier(.9, .9, .9, 1);
  --animation-duration: 250ms;
}

.gallery {
  flex-wrap: wrap;
  justify-content: space-around;
  padding-top: 120px;
  display: flex;
}

.photo-card {
  max-width: 300px;
  grid-gap: 12px;
  transition: transform var(--animation-duration) var(--timing-function);
  grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
  grid-auto-rows: 240px;
  justify-content: center;
  margin-left: auto;
  margin-right: auto;
  padding: 0;
  list-style: none;
}

.gallery__item {
  margin: 20px 0;
  text-decoration: none;
  position: relative;
  box-shadow: 0 1px 3px rgba(0, 0, 0, .2), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px -1px rgba(0, 0, 0, .12);
}

.photo-card:hover {
  transform: scale(1.01);
}

.gallery__image {
  height: 100%;
  width: 100%;
  object-fit: cover;
}

.info {
  flex-wrap: wrap;
  justify-content: space-around;
  display: flex;
}

.info-item {
  color: #187ed3;
  margin: 0;
  padding: 5px 10px;
  font-size: 14px;
  display: inline;
}

